import { useEffect, useState } from "react";
import { HStack, Heading, Box } from "@chakra-ui/react";

import { Image } from "@chakra-ui/react";
import { useParams } from "react-router-dom";

const Detail = ({ pokemon }) => {
  return (
    pokemon && (
      <Box role="pokemon-detail">
        <Heading as="h2" size="md">
          {pokemon.name}
        </Heading>

        {pokemon.types.map((tipe, index) => (
          <div key={index}>{tipe.type.name}</div>
        ))}
        <HStack>
          <Image src={pokemon.sprites.front_default} />
          <Image src={pokemon.sprites.back_default} />
          <Image src={pokemon.sprites.front_shiny} />
          <Image src={pokemon.sprites.back_shiny} />
        </HStack>
        <h2>{pokemon.height} </h2>
        <h2>{pokemon.weight} </h2>
        <h2>
          Abilities:
          {pokemon.abilities.map((daya, index) => (
            <div key={index}>
              <h2>{daya.ability.name}</h2>
            </div>
          ))}
        </h2>
        <h2>
          Stats:
          {pokemon.stats.map((power, index) => (
            <div key={index}>
              <h2>
                {power.stat.name}:{power.base_stat}
              </h2>
            </div>
          ))}
        </h2>
      </Box>
    )
  );
};
const Page = () => {
  const { id } = useParams();
  const [pokemon, setPokemon] = useState(null);

  const fetchPokemon = async (id) => {
    const response = await fetch(`https://pokeapi.co/api/v2/pokemon/${id}/`);
    const data = await response.json();
    setPokemon(data);
  };

  useEffect(() => {
    fetchPokemon(id);
  }, [id]);

  return <Detail pokemon={pokemon} />;
};

export default Page;
