import { useEffect, useState } from "react";
import { Card, HStack, CardHeader, Heading, Box, Button, SimpleGrid } from "@chakra-ui/react";
import { Link } from "react-router-dom";
import { useSearchParams, useNavigate } from "react-router-dom";
const Pagination = () => {
  let navigate = useNavigate();
  const [counter, setCounter] = useState(0);
  const [searchParams] = useSearchParams();

  useEffect(() => {
    const page = parseInt(searchParams.get("page") || 1);
    setCounter(page);
  }, [searchParams]);
  const moveTo = (direction) => {
    if (direction === "prev") {
      navigate(`/pokemon?page=${counter - 1}`);
    } else {
      navigate(`/pokemon?page=${counter + 1}`);
    }
  };
  return (
    <HStack>
      {counter <= 1 ? (
        <button disabled>Prev</button>
      ) : (
        <button value={"prev"} onClick={(e) => moveTo(e.target.value)}>
          Prev
        </button>
      )}

      <button value={"next"} onClick={(e) => moveTo(e.target.value)}>
        Next
      </button>
    </HStack>
  );
};

const PokemonList = ({ pokemons }) => {
  return (
    pokemons &&
    pokemons.length > 0 && (
      <Box role="pokemon-list">
        {pokemons.map((pokemon) => (
          <Link key={pokemon.id} to={`${pokemon.id}`}>
            <Card>
              <CardHeader>
                <Heading as="h3" size="md">
                  {pokemon.name}
                </Heading>
              </CardHeader>
              <SimpleGrid columns={[1, 2, 4]}>
                <img src={pokemon.sprites.front_default} alt="Front Default" />
                <img src={pokemon.sprites.back_default} alt="Back Default" />
                <img src={pokemon.sprites.front_shiny} alt="Front Shiny" />
                <img src={pokemon.sprites.back_shiny} alt="Back Shiny" />

                {pokemon.types.map((tipe, index) => (
                  <h2 key={index}>{tipe.type.name}</h2>
                ))}
              </SimpleGrid>
            </Card>
          </Link>
        ))}
      </Box>
    )
  );
};
